
import { defineComponent, onMounted, ref } from "vue";
import { setCurrentPageTitle } from "@/core/helpers/breadcrumb";
import BlockService from "@/store/modules/block/service/block.service";
import IBlock from "@/store/modules/block/interface/block.interface";
import Swal from "sweetalert2/dist/sweetalert2.js";
import * as animationHelper from "@/store/helpers/animation.helper";
import * as validationHelper from "@/store/helpers/validation.helper";

export default defineComponent({
	name: "Blocks",
	components: {
	},
	setup() {
		const blocks = ref<Array<IBlock>>([]);
        const blockService = new BlockService();
		const block = ref<IBlock>({
				id: null,
				name: '',
				slug: '',
				description: '',
				visible: false,
				duration: 0,
				direction: ''
		});

		const getData = () => blockService.getData()
			.then((response) => {
				response.data.forEach((element: any) => {
					element.visible = (element.visible == 1)
				});
				blocks.value = response.data
			})
			.catch((error) => {
				
			});

		const saveStatus = (block: IBlock) => blockService.changeStatus(block.id, block)
        .then((response) => {
            Swal.fire(
            'Confirmación!',
            'Información actualizada',
            'success'
            )
        })
        .catch((error) => {
            Swal.fire(
            'Oh no!',
            'No se guardaron los cambios',
            'error'
            )
        });

		const changeValue = (e: any, block: IBlock) => {
			Swal.fire({
				title: 'Confirmación!',
				text: "¿Esta seguro que desea cambiar el estado?",
				icon: 'question',
				showCancelButton: true,
				confirmButtonColor: '#3085d6',
				cancelButtonColor: '#d33',
				confirmButtonText: 'Si, Guardar',
				cancelButtonText: 'No, Cancelar',
				reverseButtons: true
				}).then((result) => {
					if (result.isConfirmed) {
						saveStatus(block);
					}
					else{
						blocks.value.forEach((element: any, index: any) => {
							if(element.id == block.id){
								blocks.value[index].visible = !block.visible;
							}
						});
					}
			})
		}

		const editAnimation = (blockData: any) => {
			block.value = blockData;
		}

		const validateBlock = () => {
			let disabled = false;
			if(!block.value.id || (block.value.slug != 'slides' && (isNaN(block.value.duration) || block.value.duration <= 0))) disabled = true;
			return disabled;
		}

		const saveAnimation = () => {
			Swal.fire({
				title: 'Confirmación!',
				text: "¿Esta seguro que desea cambiar los cambios?",
				icon: 'question',
				showCancelButton: true,
				confirmButtonColor: '#3085d6',
				cancelButtonColor: '#d33',
				confirmButtonText: 'Si, Guardar',
				cancelButtonText: 'No, Cancelar',
				reverseButtons: true
				}).then((result) => {
					if (result.isConfirmed) {
						blockService.saveAnimation(block.value.id, block.value)
						.then((response) => {
							Swal.fire(
							'Confirmación!',
							'Información actualizada',
							'success'
							)
							.then((result) => {
								getData()
								document.getElementById('animation-close-button')?.click()
							});
							
						})
						.catch((error) => {
							Swal.fire(
							'Oh no!',
							'No se guardaron los cambios',
							'error'
							)
						});
					}
			})
		}

		const getRouteBlock = (slug) => {
			let link = '';
			if(slug == 'slides') link = 'slides';
			else if(slug == 'messages') link = 'blocks-messages';
			else if(slug == 'banners') link = 'blocks-banners';
			return link;
		}
			
		onMounted(() => {
			getData()
		});

		return {
            getData,
            blocks,
			changeValue, 
			animationHelper,
			editAnimation,
			block,
			validationHelper,
			validateBlock,
			saveAnimation,
			getRouteBlock
        };
	},
});
