export function duration(duration: any) {
    let parseDuration = '';
    let hours = 0;
    let minutes = 0;
    let seconds = 0;

    if(!isNaN(duration)){
        if(duration >= 3600){
            let rest = parseInt((duration % 3600).toString());
            hours = parseInt((duration / 3600).toString());
            if(rest >= 60){
                minutes = parseInt((rest / 60).toString());
                seconds = parseInt((rest % 60).toString())
            }
        }
        else if(duration >= 60){
            minutes = parseInt((duration / 60).toString());
            seconds = parseInt((duration % 60).toString())
        }
        else{
            seconds = duration;
        }
    }

    parseDuration = `${((hours < 10)?'0':'')+hours}:${((minutes < 10)?'0':'')+minutes}:${((seconds < 10)?'0':'')+seconds}`;
    return parseDuration;
};

export function direction(direction: string){
    return ((direction == 'right')?'Derecha':'Izquierda');
}