import ApiService from "@/core/services/ApiService";
import IBlock from "@/store/modules/block/interface/block.interface"; 
import JwtService from "@/core/services/JwtService";
import axios from 'axios';
import {httpOptionsFile, httpOptions} from '../../global';

const apiServie = new ApiService();
export default class BlockService{
  getData() {
    return axios.get(`${ApiService.vueInstance.axios.defaults.baseURL}/block`, httpOptions());
  }

  changeStatus(id: any, data: any) {
    return axios.patch(`${ApiService.vueInstance.axios.defaults.baseURL}/block/${id}/status`, data, httpOptions());
  }

  saveAnimation(id: any, data: any){
    return axios.patch(`${ApiService.vueInstance.axios.defaults.baseURL}/block/${id}/animation`, data, httpOptions());
  }
}
